@mixin keyframes($name) {
    @-webkit-keyframes #{$name} {
        @content; }

    @-moz-keyframes #{$name} {
        @content; }

    @-ms-keyframes #{$name} {
        @content; }

    @keyframes #{$name} {
        @content; } }

@include keyframes(shaking) {
    0% {
        transform: rotate(0deg); }

    25% {
        transform: rotate(-10deg); }

    50% {
        transform: rotate(0deg); }

    75% {
        transform: rotate(10deg); }

    100% {
        transform: rotate(0deg); } }


@include keyframes(rotate360) {
    0% {
        transform: rotate(0deg); }

    100% {
        transform: rotate(360deg); } }


@include keyframes(map-loop) {
    0% {
        transform: translateX(0); }

    100% {
        transform: translateX(-50%); } }


@include keyframes(slow-appear) {
    0% {
        opacity: 0; }

    20% {
        opacity: 1; }

    100% {
        opacity: 1;
        transform: translateY(40%); } }

.fade-up {
    transition: 500ms;
    opacity: 0;
    transform: translateY(100px); }

.appear-animation {
    opacity: 1;
    transform: translate(0); }

