.admin-page {
	height: 100%;

	.login-form {
		.input-group {

			.label {

				.title {
					font-family: sans-serif;
					font-size: 16px;
					font-weight: 400;
				}

				input {
					border: 1px solid rgb(206, 212, 218);
					border-radius: 6px;
					width: 300px;

					font-family: sans-serif;
					font-size: 24px;
					font-weight: 400
				}
			}

		}

		.btns-group {
			.btn {
				font-family: sans-serif;
				font-size: 20px;
				font-weight: 400;
				color: #fff;

				border: none;
				outline: none;

				border-radius: 6px;
			}

			.btn-1 {
				background-color: #007bff;
				padding: 12px 26px;

				&:active {
					background-color: #0069d9;
				}
			}

			.btn-2 {
				background-color: #dc3545;
				padding: 7px 26px;

				img {
					position: relative;
					bottom: -2px;
				}

				&:active {
					background-color: #c82333;
				}
			}
		}
	}

	.messages-section {

		width: 100%;

		.btns-panel {

			.refresh-btn {
				position: relative;
				background-color: #17a2b8;
				border-radius: 10px;

				&:active {
					background-color: #138496;
				}

				.counter {
					position: absolute;
					color: #fff;
					background-color: #dc3545;
					font-size: 16px;
					transform: translate(50%, 50%);
					top: -40%;
					left: -30%;
					border-radius: 50%;
				}
			}

		}

		.messages-table {
			border-collapse: collapse;
			background-color: #343a40;

			* {
				font-size: 16px;
				text-align: center;
				color: #fff;
			}

			th, td {
				border: 2px solid #454d55;
				padding: 10px;
				margin: 0;
			}

			th {
				font-weight: 700;
			}

			td {


				.toggle-btn {
					border-radius: 4px;
					width: 100%;
					height: 100%;
					background-color: #28a745;

					&:active {
						background-color: #218838;
					}
				}

				.remove-btn {
					border-radius: 4px;
					background-color: #dc3545;

					&:active {
						background-color: #c82333;
					}
				}

			}

			td.message-text {
				background-color: #6c757d;
			}

		}

	}
}

