#feedback-section {
	position: relative;
	background-color: #fafafa;
	z-index: 3000;
	width: 100%;

	.content {
		width: 100%;
		max-width: 1200px;

		* {
			font-family: "Open Sans", Arial, sans-serif;
			color: #252525;
		}

		.title {
			font-size: 32px;
			font-weight: 500;
			text-shadow: 1px 1px 1px rgba(0,0,0,.1);
			text-transform: uppercase;
		}

		.hr-line {
			border-bottom: 3px solid #A55A5A;
		}

		form {
			width: 100%;
			position: relative;
			display: grid;
			grid-template-columns: 1fr;
			grid-column-gap: 10px;
			grid-row-gap: 20px;
			justify-content: center;

			.input-group {
				flex-wrap: wrap;

				.label {

					position: relative;
					flex: 1;
					margin: 10px 5px 0;

					input {
						width: 100%;
						min-width: 300px;
					}

				}

			}


			.label {
				.title {
					font-size: 20px;
					text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
				}

				input, textarea {
					padding: 8px 16px;
					width: 100%;
					font-family: sans-serif;
					font-size: 20px;
					font-weight: 400;
					border: 1px solid #c6c6c6;
					resize: none;

					&::placeholder {
						font-family: sans-serif;
						font-size: 20px;
						font-weight: 400;
						color: #6c757d;
					}
				}

				.error {
					color: crimson;
					font-size: 14px;
				}
			}

			.label-4 {
				margin: 0 5px;
			}

			.submit-btn {
				justify-self: center;
				background-color: #a55a5a;
				border: none;
				border-radius: 4px;

				span {
					font-family: sans-serif;
					font-size: 18px;
					font-weight: 400;
					color: #ffffff;
					text-transform: uppercase;
				}
			}
		}

	}

}