#page-navbar {
	top: 0;
	width: 100%;
	z-index: 99999;
	height: 100px;
	background-color: transparent;
	display: flex;
	align-items: center;
	transition: 300ms;

	@media screen and (max-width: 1120px) {
		display: none;
	}

	* {
		font-family: "Open Sans", Arial, sans-serif;
		font-size: 18px;
		font-weight: 400;
		color: #fff;
	}

	.menu {
		flex: 1;

		.item {
			text-transform: uppercase;
			transition: 300ms;

			&:hover {
				color: #a55a5a;

				svg {
					* {
						transition: 300ms;
						fill: #a55a5a !important;
					}
				}
			}
		}

		.item-1 {
			.main-logo {
				height: 81px;
				width: auto;
			}
		}

	}
}

#page-navbar.scrolled {

	height: 90px;
	background-color: #fff;

	* {
		font-weight: 500;
		color: #252525;
		text-shadow: 1px 1px 1px rgba(0,0,0,.1);

		svg {
			* {
				fill: #252525 !important;
			}
		}
	}



}
