#footerbar {
	position: relative;
	width: 100%;
	z-index: 3000;
	background-color: #fafafa;

	* {
		font-family: "Open Sans", Arial, sans-serif;
		color: #252525;
		text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
	}

	.content {
		width: 100%;
		max-width: 1170px;

		.copyright {

			.title {
				font-size: 20px;
			}

			.caption {
				font-size: 14px;
			}
		}

		.menu {

			.item {
				font-size: 14px;
			}
		}

	}

	@media screen and (max-width: 1120px) {
		.content {
			flex-direction: column-reverse;
			align-items: center;

			.menu {
				flex-direction: column;

				.item {
					margin-top: 10px;
					margin-bottom: 10px;
				}
			}

			.copyright {
				margin-top: 20px;

				.title {
					text-align: center;
					font-size: 20px;
				}

			}
		}
	}
}