#page-mobile-navbar {
	display: none;
	width: 100%;
	z-index: 9000;
	top: 0;
	left: 0;
	background-color: #ffffff;

	svg {
		* {
			fill: #252525 !important;
		}
	}

	* {
		font-family: "Open Sans", Arial, sans-serif;
		font-size: 20px;
		font-weight: 500;
		color: #fff;
		text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
		text-transform: uppercase;
	}


	& > .item-1 {
		flex: 0 0 60px;
	}

	& > .item-2 {
		flex: 1;
	}

	& > .item-3 {
		flex: 0 0 60px;
	}

	.menu {
		background-color: #030303d9;
		top: 0;
		width: 100%;
		height: 100vh;
		transition: 300ms;

		&.hidden {
			transform: translateY(-100%);
		}

		.close-btn {
			top: 15px;
			right: 15px;

			* {
				fill: #ffffff !important;
			}
		}

		.menu-list {

			.item {
				height: fit-content;
				transition: 300ms;

				&:hover {
					color: #a55a5a;
				}
			}

			.item-5 {
				border-top: 1px solid rgba(235,235,235,0.6);

				svg {
					* {
						transition: 300ms;
						fill: #a55a5a !important;
					}

					&:hover * {
						fill: #ffffff !important;
					}
				}
			}

		}
	}

	@media screen and (max-width: 1120px) {
		display: flex;
	}

}
