.infopage-section {

	position: relative;
	height: 100%;

	* {
		font-family: Roboto, sans-serif;
		font-weight: 400
	}

	.info-list {
		margin-left: 5%;
		padding-right: 5%;

		.item {
			font-size: 22px;
			color: black;
		}

	}

	.description {
		margin-right: 5%;
		padding-left: 5%;

		.title {
			font-size: 22px;
		}

		.list {
			.item {
				font-size: 18px;
			}
		}

		.caption {
			font-size: 22px;
		}
	}

}