#word-directions-section {
	position: relative;
	z-index: 3000;
	background-color: #fff;
	width: 100%;
	background-color: #fafafa;

	.part-1 {

		.content {
			width: 100%;
			max-width: 1240px;

			* {
				font-family: "Open Sans", Arial, sans-serif;
				color: #252525;
			}

			.title {
				font-size: 32px;
				font-weight: 500;
				text-shadow: 1px 1px 1px rgba(0,0,0,.1);
			}

			.hr-line {
				border-bottom: 3px solid #A55A5A;
			}

			.card-list {
				width: 100%;
				display: grid;
				grid-template-columns: repeat(auto-fill, 280px);
				grid-row-gap: 60px;
				grid-column-gap: 30px;
				justify-content: center;

				.card {
					width: 280px;

					.title {
						font-size: 20px;
						font-weight: bold;
						text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
					}

					.caption {
						font-size: 16.5px;
						line-height: 24.75px;
					}

					.link {
						background-color: #a55a5a;
						color: #fff;
						border-radius: 4px;
						justify-self: flex-end;
					}

				}
			}

		}
	}

	.part-2 {
		padding: 100px 0;
		background: linear-gradient(to right, #355c7d, #6c5b7b, #c06c84);

		* {
			font-family: "Open Sans", Arial, sans-serif;
			color: #fff;
		}

		.content {
			width: 100%;
			max-width: 1140px;


			.title {
				font-size: 36px;
				font-weight: 700;
				line-height: 54px;
			}

			.list {

				.item {
					font-size: 18px;
					font-weight: 400;
				}
			}

		}
	}

}