html {
	font-family: "Roboto", sans-serif;

}

* {
	box-sizing: border-box !important;
}

html, body, #app {
	height: 100%;
	margin: 0;
}

a {
	text-decoration: none;
	color: #000000;
}

ul {
	margin: 0;
	padding: 0;
}

li {
	list-style-type: none;
}

// FLEXBOX-LAYOUT
.f-grow-1 {
	flex-grow: 1;
}

.flex {
	display: flex;
}

.flex-column {
	display: flex;
	flex-direction: column;
}

.flex-safe {
	display: flex;
	justify-content: safe center;
}

.flex-unsafe {
	display: flex;
	justify-content: unsafe center;
}

.flex-align-center {
	display: flex;
	align-items: center;
}

.flex-justify-center {
	display: flex;
	justify-content: center;
}

.centered {
	display: flex;
	justify-content: center;
	align-items: center;
}

@each $attribute in center, end, space-between, space-around, space-evenly {
	.flex-#{$attribute} {
		display: flex;
		@if $attribute == end {
			justify-content: flex-end;
		} @else {
			justify-content: #{$attribute};
		}
	}
}

//////////

// HEIGHT & WIDTH

@for $i from 1 through 150 {
	.h-#{$i} {
		height: $i+ px;
	}

	.w-#{$i} {
		width: $i+ px;
	}
}

.h-inherit {
	height: inherit;
}

.w-inherit {
	width: inherit;
}

.w-h-inherit {
	height: inherit;
	width: inherit;
}

.w-fit {
	width: fit-content !important;
}

.w-max {
	width: max-content !important;
}

.w-min {
	width: min-content !important;
}

//////////

// MARGIN FROM 1px TO 100px
@for $i from 0 through 100 {
	.mt-#{$i} {
		margin-top: $i+ px !important;
	}

	.pt-#{$i} {
		padding-top: $i+ px !important;
	}
	.mb-#{$i} {
		margin-bottom: $i + px !important;
	}
	.mr-#{$i} {
		margin-right: $i + px !important;
	}
	.ml-#{$i} {
		margin-left: $i + px !important;
	}
	.pr-#{$i} {
		padding-right: $i + px !important;
	}
	.pl-#{$i} {
		padding-left: $i + px !important;
	}
	.pb-#{$i} {
		padding-bottom: $i + px !important;
	}
	.p-#{$i} {
		padding: $i + px !important;
	}
	.m-#{$i} {
		margin: $i + px !important;
	}
	.py-#{$i} {
		padding-top: $i + px !important;
		padding-bottom: $i + px !important;
	}
	.px-#{$i} {
		padding-left: $i + px !important;
		padding-right: $i + px !important;
	}
	.my-#{$i} {
		margin-top: $i + px !important;
		margin-bottom: $i + px !important;
	}
	.mx-#{$i} {
		margin-left: $i + px !important;
		margin-right: $i + px !important;
	}
}

.mt-auto {
	margin-top: auto;
}

.mb-auto {
	margin-bottom: auto;
}

.ml-auto {
	margin-left: auto;
}

.mr-auto {
	margin-right: auto;
}

.m-auto {
	margin: auto;
}

.mx-auto {
	margin-left: auto;
	margin-right: auto;
}

.my-auto {
	margin-top: auto;
	margin-bottom: auto
}

// UTILS

.pointer {
	cursor: pointer;
}

.text-center {
	text-align: center;
}

.hover_underline {
	border-bottom: 1px solid transparent;

	&:hover {
		border-bottom: 1px solid #fff;
	}
}

.hover_background {

	&:hover {
		background-color: #F2F2F2;
	}
}

//////////

// POSITION

.absolute {
	position: absolute;
}

.relative {
	position: relative;
}

.fixed {
	position: fixed;
}

//////////

// Z-INDEX

@for $i from 1 through 10 {
	.zIndex-#{100 * $i} {
		z-index: 100 * $i;
	}
}

//////////

// MISC

.select-none {
	user-select: none !important;
}

.bor-none {
	border: none !important;
}

.out-none {
	outline: none !important;
}

//////////

// DISPLAY

.d-none {
	display: none;
}

.d-block {
	display: block;
}

//////////