#info-section {
	position: relative;
	z-index: 3000;
	background-color: #fff;
	width: 100%;
	border-bottom: 1px solid #EBEBEB;

	.content {
		position: relative;
		width: 100%;
		max-width: 1260px;

		* {
			font-family: "Open Sans", Arial, sans-serif;
			color: #252525;
		}

		.title {
			font-size: 32px;
			font-weight: 500;
			text-shadow: 1px 1px 1px rgba(0,0,0,.1);
			text-transform: uppercase;
		}

		.text {
			font-family: sans-serif;
			font-size: 22px;
			font-weight: 400;
			text-align: center;
		}

		.hr-line {
			border-bottom: 3px solid #A55A5A;
		}

		.card-list {
			width: 100%;
			display: grid;
			grid-template-columns: repeat(auto-fill, 275px);
			grid-auto-rows: 244px;
			grid-row-gap: 20px;
			grid-column-gap: 40px;
			justify-content: center;


			.card {

				.icon-wrapper {
					width: 120px;
					height: 120px;
					border-radius: 50%;
					border: 5px solid transparent;
				}

				hr {
					padding: 0;

					width: 90%;
					color: #c6c6c6;
					border: 1px solid #c6c6c6;
				}

				.title {
					font-size: 20px;
					font-weight: bold;
					text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
				}

				.text {
					font-size: 18px;
					font-weight: 400;
				}
			}

			.card-1 {

				&:hover .icon-wrapper .worldmap-icon-wrapper .img-group {
					animation: map-loop 2s linear 0s infinite;
					animation-fill-mode: both;
				}

				.icon-wrapper {
					.worldmap-icon-wrapper {
						border: 3px solid #a55a5a;
						border-radius: 50%;
						overflow: hidden;


						.icon-2 {
							position: relative;
						}

						.img-group {
							display: flex;
							height: fit-content;
							width: fit-content;

						}
					}
				}
			}

			.card-2 {

				&:hover .icon-wrapper #clocks__icon {

					#hours-hand_icon {
						animation-name: rotate360;
						animation-duration: 4s;
						animation-timing-function: linear;
						animation-iteration-count: infinite;
					}

					#seconds-hand_icon {
						animation-name: rotate360;
						animation-duration: 2s;
						animation-timing-function: linear;
						animation-iteration-count: infinite;
					}

				}


				.icon-wrapper {

					#clocks__icon {

						#hours-hand_icon,
						#seconds-hand_icon {
							transform-origin: 50% 50%;
						}
					}

					&.ms-edge {
						#clocks__icon {

							#hours-hand_icon {
								transform-origin: 50% 80%
							}

							#seconds-hand_icon {
								transform-origin: 64% 33.5%
							}
						}
					}
				}
			}

			.card-3 {
				.icon-wrapper {
					width: 121px;
				}

				&:hover .icon-wrapper #mobile__icon {

					#phone {
						animation-name: shaking;
						animation-duration: 2s;
						animation-timing-function: linear;
						animation-iteration-count: infinite;
					}

					#screen {
						opacity: 1;
						transition: 1s;
					}

					#closest__wave {
						opacity: 1;
						transition: 0.5s;
					}

					#further__wave {
						opacity: 1;
						transition: 1s;
					}

				}

				.icon-wrapper {

					#mobile__icon {

						#phone {
							transition: 1s;
							transform-origin: 50% 50%
						}

						#screen,
						#closest__wave,
						#further__wave {
							opacity: 0;
						}
					}
				}

			}

			.card-4 {

				&:hover .icon-wrapper > div #email__icon {

					#letter {
						animation-name: slow-appear;
						animation-duration: 1.5s;
						animation-fill-mode: forwards;
					}

					#cap {
						transition: 0.3s;
						transform: scale(1, -1);
					}

				}

				.icon-wrapper {
					& > div {
						position: relative;
						top: -42px;

						#email__icon {

							#letter {
								opacity: 0;

								* {
									font-size: 45px;
								}

							}

							#cap {
								transform-origin: 50% 56.8%;
							}

						}
					}

					&.ms-edge > div {

						#email__icon {

							#cap {
								transform-origin: 50% 2%;
							}

						}

					}
				}
			}
		}

	}

}