#homepage-section {

	position: relative;
	z-index: 2000;
	height: 100vh;

	.background {
	  	position: fixed;
		width: 100vw;
		height: 100vh;
		background-size: cover;
		background-repeat: no-repeat;
		filter: contrast(85%) brightness(100%) saturate(75%) sepia(22%);

		.inner-background {
			height: 100%;
			width: 100%;
			background-color: #000;
			opacity: 0.4;
		}
	}

	header {
		width: 100%;
		max-width: 1140px;

		* {
			font-family: "Open Sans", Arial, sans-serif;
			color: #fff;
		}

		.title {
			font-size: 60px;
			font-weight: 700;
			background-color: rgba(165,90,90,0.7);
			text-shadow: 3px 3px 3px #000;
		}

		.caption {
			font-size: 35px;
			font-weight: 400;
			text-shadow: 1px 1px 3px #000;
			border: 1px solid #fff;
			border-left: none;
			border-right: none;
		}

		.description {
			font-size: 30px;
			font-weight: 400;
			text-shadow: 3px 3px 3px #000;
		}

	}

	@media screen and (max-width: 1120px) {
		header {
			.caption {
				font-size: 26px;
			}

			.description {
				font-size: 24px;
			}
		}
	}
}