#map-section {
	position: relative;
	z-index: 3000;
	background-color: #fff;
	.map-cover-box {
		height: 100%;
		width: 100%;
		z-index: 3000;
		background-color: rgba(0,0,0,.3);
		border-bottom: 1px solid #EBEBEB;

		button {
			border: none;
			outline: none;
			border-radius: 6px;
			padding: 8px 16px;
			background-color: #a55a5a;

			font-family: "Open Sans", Arial, sans-serif;
			font-size: 18px;
			font-weight: 400;
			color: #fff;
			text-transform: uppercase;

			transition: 100ms;

			&:active {
				color: #fff;
				background-color: #a67b7b;
				border-color: #545b62;
			}
		}
	}
}